.menu-page {
	display: flex;
	width: 255px;
	flex-direction: column;
	background-color: #fff;
	margin: 0 0 20px 0;
	/*padding: 40px 0 30px 0;*/
	padding: 39px 0 30px 0;
	overflow: hidden;
	&__link {
		color: #808080;
		display: block;
		position: relative;
		padding: 0 0 0 65px;
		/*margin: 0 0 30px 0;*/
		font-size: 14px;
		span {
			/*position: absolute;*/
			/*border: solid #9b9b9e;
			border-width: 0 1px 1px 0;*/
			display: inline-block;
			padding: 4px;
			/*transform: rotate(-45deg);*/
			right: 25px;
			bottom: -10px;
			transition: all 0.3s ease;
			cursor: pointer;
		}
		&-arrow {
			&::after {
				content: "";
				position: absolute;
				top: 10px;
				left: -100%;
				width: 28px;
				height: 28px;
				background-color: $colorOne;
				border-radius: 100%;
				transition: all 0.3s ease;
			}
		}
		&::before {
			content: "";
			position: absolute;
			top: 15px;
			/*top: 0;*/
			left: 25px;
			width: 21px;
			height: 21px;
		}
		&.active {
			color: $colorOne;
			&::after {
				left: 0%;
				transform: translate(-50%, 0);
				transition: all 0.3s ease;
			}
			span {
				/*position: absolute;*/
				/*border: solid $colorOne;
				border-width: 0 1px 1px 0;*/
				display: inline-block;
				padding: 4px;
				/*transform: rotate(45deg);*/
				right: 25px;
				top: 0;
				transition: all 0.3s ease;
				cursor: pointer;
			}
		}
		&-01 {
			&::before {
				background: url("../../assets/img/side/side_link_01.svg") 0 0 / cover no-repeat;
			}
			&.active{
				&::before {
					background: url("../../assets/img/icons/money_green.svg") 0 0 / cover no-repeat;
				}
			}
		}
		&-02 {
			&::before {
				background: url("../../assets/img/side/side_link_02.svg") 0 0 / cover no-repeat;
			}
		}
		&-03 {
			&::before {
				background: url("../../assets/img/side/side_link_03.svg") 0 0 / cover no-repeat;
			}
		}
		&-04 {
			&::before {
				background: url("../../assets/img/side/side_link_04.svg") 0 0 / cover no-repeat;
			}
		}
		&-05 {
			&::before {
				background: url("../../assets/img/side/side_link_05.png") 0 0 / cover no-repeat;
			}
		}
		&-06 {
			&::before {
				background: url("../../assets/img/side/side_link_06.svg") 0 0 / cover no-repeat;
			}
		}
		&-07 {
			&::before {
				background: url("../../assets/img/side/side_link_07.svg") 0 0 / cover no-repeat;
			}
			&.active{
				&::before {
					background: url("../../assets/img/icons/pen_green.svg") 0 0 / cover no-repeat;
				}
			}
		}
		&-08 {
			&::before {
				background: url("../../assets/img/side/side_link_08.svg") 0 0 / cover no-repeat;
			}
		}
		&-09 {
			&::before {
				background: url("../../assets/img/side/side_link_09.svg") 0 0 / cover no-repeat;
			}
		}
	}
	&__item {
		margin-bottom: 10px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__sub-link {
		color: $colorNine;
		display: block;
		padding: 0 0 0 25px;
		font-size: 14px;
		&.active {
			color: $colorTwenty;
		}
	}

	&__list {
		margin: 0 0 30px 0;
		display: none;
	}

	&__btn {
		align-self: center;
		position: relative;
		top: 20px;
		left: 50px;
		&::before {
			content: "";
			position: absolute;
			top: 50%;
			transform: translate(0, -50%);
			left: 15px;
			@include adaptiv-value("width", 16, 12, 0);
			@include adaptiv-value("height", 16, 12, 0);
			background: url("../../assets/img/icons/exit.svg") 0 0 / cover no-repeat;
			z-index: 1;
		}
	}
}
