.bonus-side {
	position: relative;
	padding: 30px 80px 30px 80px;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	margin: 0 0 15px 0;
	&__label {
		color: #ffffff;
		font-size: 18px;
		position: relative;
		z-index: 1;
	}
	&__price {
		color: #ffffff;
		font-size: 32px;
		font-weight: 500;
		position: relative;
		z-index: 1;
		span {
			position: relative;
			&::before {
				content: "";
				position: absolute;
				top: 10px;
				left: 0;
				width: 20px;
				height: 20px;
				background: url("../../assets/img/side/uah.svg") 0 0 / cover no-repeat;
			}
		}
	}

	&__bg {
		border-radius: 5px;
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
	}
}

//...................................................................................................

.action-side {
	position: relative;
	padding: 33px 121px 214px 14px;
	&__title {
		color: #ffffff;
		font-size: 32px;
		font-weight: 500;
		position: relative;
		z-index: 1;
	}

	&__bg {
		border-radius: 5px;
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
	}
	&__bgphone {
		display: none;
	}
}
